.login-register-page .title {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 1.8rem; }

.login-register-page .paper-style {
  height: auto;
  width: 400px;
  padding-top: 10px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 30px;
  text-align: center;
  margin-bottom: 30px; }

.login-register-page .facebook-google-icon {
  margin-left: 2px;
  margin-right: 2px; }

.login-register-page .forgot-password {
  color: #3468DD;
  float: left;
  text-decoration: none;
  cursor: pointer; }

.login-register-page .create-account {
  color: #3468DD;
  margin-left: 10px;
  text-decoration: none; }

@media all and (max-width: 480px) {
  .login-register-page .title {
    font-size: 1.5rem; }
  .login-register-page .paper-style {
    width: 360px; } }

@media all and (max-width: 320px) {
  .login-register-page .title {
    font-size: 1.3rem; }
  .login-register-page .paper-style {
    width: 300px;
    padding: 10px 30px 20px 30px; } }



